import { html, render, TemplateResult } from 'lit-html';

function evalInContext(js: string) {
    return eval(js);
}

const cache: { [key: string]: string } = {};

export class Element extends HTMLElement {

    template: any;
    templateData: any = {};
    templateUrl: string;

    async connectedCallback() {

        const className = this.constructor.name.toLowerCase();

        if (!cache[className]) {
            // get the template
            const res = await fetch(this.templateUrl);
            cache[className] = await res.text();
        }

        // cache the template

        this.template = (data: any) => {
            data.__html = html;
            const js = `this.__html\`${cache[className]}\``;
            const templateResult = evalInContext.call(data, js);
            return templateResult;
        };
        this.render();
    }

    render() {
        render(this.template(this.templateData), this);
    }

}
